@import '../../Styles/Colors.scss';
$technologies-background: rgba(128,128,128,0.15);
$technologies-high-level-color: $color-red-3;
$technologies-low-level-color: rgba(0,0,0,0.25);
.skills-component {
    padding: 1rem;

    .main-skill-container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 0px;
    }

    .skill {
        width: 1fr;
        height: 7rem;
        background-color: $technologies-background;
        border-radius: 25px;
        padding: 1rem 1rem;
        margin: 1rem 1rem;
        display: flex;
        justify-content: center;

        .main-skill-level {
            display: inline;
            align-self: center;
        }
        
        .main-skill-icon {
            width: 5rem;
            height: auto;
            object-fit: contain;
            display: inline-flex;
            margin: 1rem 1rem;
        }

        .main-skill-exp{
            display: flex;
            text-transform: uppercase;
            margin-left: 0.5rem;
            margin-right: 0.5rem;
            margin-bottom: 0.5rem;
        }

        .level-meter {
            display: inline-flex;
        }

        %level-point {
            border-radius: 100%;
            width: 2rem;
            height: 2rem;
            margin: 0rem 0.5rem;
        }
    
        .level-on {
            @extend %level-point;
            background-color: $technologies-low-level-color;
        }
        .level-off {
            @extend %level-point;
            background-color: $technologies-high-level-color;
        }
    }

    .skill-minor {
        display: inline;
        width: 80%;

        .minor-skill-container {
            width: 4rem;
            height: 4rem;
            background-color: rgba(255,255,255,0.9);
            border-radius: 10px;
            margin: 1rem 1rem;
            padding: 0.5rem 0.5rem;
            display: inline-flex;
            justify-items: center;
           
            .minor-skill {
                width: 100%;
                object-fit: contain;
                display: inline-flex;
            }
        }
    }
}