.experience-job-title-component {

    .experience--time {
        text-align: center;
    }
    
    .experience--logo {
        width: 12rem;
        height: auto;
        margin-bottom: 2rem;
        border-radius: 25px;
        padding: 1rem;
    }

    .experience--role {
        font-weight: bold;
        text-transform: uppercase;
    }
}

.experience--container {
    padding: 1rem 0rem;
}

.projects-navigation {
    margin: 1rem 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    .navigation-dot {
        display: flex;
        width: 32px;
        height: 32px;
        border-radius: 100%;
        margin: 0rem 0.2rem;
        &.selected-dot {
            background-color: rgba(255,255,255, 0.70);
        }

        &.normal-dot {
            background-color: rgba(255,255,255,0.30);
        }
    }
}

.job {
    .job-projects-container{
        display: flex;
        padding: 1rem 1rem;
    }
    .job-skill-text-container {
        display: flex;
        margin: 0.7rem auto;
        justify-content: center;
        .job-skill-text {
            display: flex;
            font-size: 16px;
            text-transform:uppercase;
            text-align: left;
            width: 24rem;
            line-height: 26px;
        }
        .job-skill-text-point {
            display: flex;
            font-weight: 700;
            margin: 0rem 1rem;
        }
    }
   
}



@keyframes fadein {
    from { opacity: 0.3; }
    to   { opacity: 1; }
}

@keyframes fadeout {
    from { opacity: 1; }
    to   { opacity: 0.3; }
}

.job-project-card {
    background-color: rgba(0,0,0,0.10);
    padding: 1rem 1rem;
    border-radius: 25px;
    height: 30rem;
    width: 22rem;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    margin-bottom: 2rem;
    float: none;

    &.card-inactive {
        display: none !important;
        animation: fadeout 1s;
    }

    &.card-active {
        animation: fadein 1s;
    }

    .project-logo-container{
        background-color: white;
        border-radius: 25px;
        min-height: 12rem;
        padding: 1rem 1rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        .project-logo {
            max-height: 10rem;
            max-width: 10rem;
            padding: 1rem 1rem;
            margin: 0rem 1rem;
            display: flex;
        }
        .project-title {
            max-width: 10rem;
            color: black;
            display: flex;
            text-align: center;
        }
    }

    .project-tech-stack {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: 100%;
        margin: 1rem 1rem;

        .tech-stack-icon-container {
            display: flex;
            margin: 1rem 1rem;
            flex-basis: content;
            .tech-stack-icon {
                padding: 0.3rem 0.3rem;
                flex-grow:1;
                width: 100%;
                object-fit: contain;
                height: auto;
                max-width: 4rem;
                max-height: 4rem;
                background-color: white;
                border-radius: 25px;
            }
        }
    }
}


.job-skill-container {
    padding-left: 1rem;
    padding-right: 1rem;
    line-height: 1.7rem;
    padding-bottom: 1rem;
}
