@font-face {
  font-family: 'OpenSans';
  src: local('OpenSans'), url(Fonts/OpenSans-Regular.ttf) format('opentype');
}

@font-face {
  font-family: 'OpenSans';
  src: local('OpenSans'), url(Fonts/OpenSans-Bold.ttf) format('opentype');
  font-weight: bold;
}

@font-face {
  font-family: 'DancingScript';
  src: local('DancingScript'), url(./Fonts/DancingScript-Regular.ttf) format('opentype');
}
@import './Styles/Colors.scss';

div {
  color: white;
  font-family: 'OpenSans';
}

html {
  background-color: $main-background-color;
}

.App {
  min-width: 40rem;
  max-width: 80rem;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}

.text-center {
  display: flex;
  text-align: center;
  align-items: center;
}

.text--category {
  width: auto;
  font-size: 3rem;
  text-align: center;
  text-transform: uppercase;
  border-bottom: 1px solid white;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}

.text--subcategory {
  font-size: 2.2rem;
  text-align: center;
  text-transform: uppercase;
}

.text--normal {
  font-size: 1.2rem;
}

.text--center {
  text-align: center;
}

.bold {
  font-weight: bold;
}

.img--center {
  display: block;
  margin: 0 auto;
}

.container-base {
  background-color: $container-background-color;
  border-bottom: 5px double $color-base-2;
  border-right: 5px double $color-base-2;
  border-top: 1px solid $color-base-2;
  border-left: 1px solid $color-base-2;
  border-radius: 15px;
  
  margin-bottom: 1rem;
}

@keyframes pulse {
  0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 100%;
	}
	100% {
		background-position: 0% 50%;
	}
}


.half-container {
  width: 50%;
  display: flex;
  flex-direction: column;

  &.white-container {
    background: linear-gradient(135deg, $color-base-2, $color-red-3);
    background-size: 200% 200%;
    animation: pulse 12s ease infinite;
    div {
      color: black;
    }
  }

  &.black-container {
    div {
      color: white;
    }
  }
}

.inline-container {
  display: flex;
}
