@import '../Styles/Colors.scss';

.Navbar {
    width: 100%;
    height: 4rem;
    border: 1px solid $color-base-2;
    display: flex;
    justify-content: space-between;
}

.navigation {
    height: 100%;
    padding-right: 2rem;
    display: flex;
    justify-content: center;
}

.logo {
    font-size: 2rem;
    font-weight: bold;
    color: white;
    font-family: 'DancingScript';
    text-align: left;
    margin: auto 0;
    padding-left: 2rem;
}