@import './Colors.scss';
.button-fixedsize {
    background-color: transparent;
    border: none;
    background: none;
    color: white;
    height: 100%;
    font-size: large;
    box-shadow: none;
    height: 3rem;
    width: 10rem;
    margin: auto 0;
}



.button-base {
    background-color: transparent;
    border: none;
    background: none;
    color: white;
    font-size: large;
    box-shadow: none;
    margin: auto 0;
}

.button-circle {
    padding: 1rem 1rem;
    border-radius: 100%;
    border: 3px solid white !important;
    background: none;
    color: white;
    display: flex !important;
    justify-content: center;
    align-items: center;
}

.button--bordered {
    border-radius: 25px;
    border: 3px solid white !important;
}

.button--selected {
    background-color: $color-red-3;
}