@import '../../Styles/Colors.scss';
.Intro {
    display: flex;

    .intro--name {
        font-size: 8rem;
        width: 8rem;
        line-height: 6.2rem;
        color:$color-red-3;
        font-weight: 900;
    }
    
    .intro--picture {
        height: 23rem;
        width: auto;
    }
    
    .intro--leftside {
        display: inline;
        width: 50%;
        padding-left: 2rem;
        padding-top: 1rem;
    }
    
    .intro--rightside {
        width: 50%;
    }
    
    .linked-in {
        margin-top: 1rem;
        padding: 1rem 1rem;
        background-color: transparent;
        border-radius: 100%;
        border: 3px solid $color-red-3 !important;

        .linked-in-icon {
            width: 2rem;
            height: 2rem;
        }
    }
}
